import React from 'react';
import Slider from 'react-slick';
import './carousel.css';

class Carousel extends React.Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 400,
      slidesToShow: 5,
      slidesToScroll: 2,
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 2,
          },
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
          },
        },
        {
          breakpoint: 576,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
          },
        },
      ],
    };

    return (
      <div className="carousel-container">
        <div className="slider-container">
          <div className="content-wrapper">
            {/* <p>Cars</p> */}
            <Slider {...settings}>
              <div className="slider-image-container">
                <div className="image toyota"></div>
              </div>
              <div className="slider-image-container">
                <div className="image audi"></div>
              </div>
              <div className="slider-image-container">
                <div className="image bmw"></div>
              </div>
              <div className="slider-image-container">
                <div className="image ford"></div>
              </div>
              <div className="slider-image-container">
                <div className="image hyundai"></div>
              </div>
              <div className="slider-image-container">
                <div className="image toyota"></div>
              </div>
              <div className="slider-image-container">
                <div className="image audi"></div>
              </div>
              <div className="slider-image-container">
                <div className="image bmw"></div>
              </div>
              <div className="slider-image-container">
                <div className="image ford"></div>
              </div>
              <div className="slider-image-container">
                <div className="image hyundai"></div>
              </div>
            </Slider>
          </div>
        </div>
      </div>
    );
  }
}

export default Carousel;


