import React from 'react';
import Carousel from '../../components/carousel';

import './get-quote-page.css';

class GetQuotePage extends React.Component {
  render() {
    return (
      <section className="get-quote-section">
        <div className="landing-banner quote">
          <div className="text-container">
            <p className="landing-header">Sell your Car for Cash</p>
            <p className="landing-subtext">Trusted by over 5 million users</p>
          </div>
        </div>
        <div className="sell-fast-container">
          <div className="content-container">
            <h1 className="text-center">SELL FAST EASY</h1>
            <div className="row mb-5">
              <div className="col">
                <select className="custom-select" id="inputGroupSelect01">
                  <option selected>Please Select Year</option>
                  <option value="1">2018</option>
                  <option value="2">2017</option>
                  <option value="3">2016</option>
                </select>
              </div>
              <div className="col">
                <select className="custom-select" id="inputGroupSelect01">
                  <option selected>Please Select Model</option>
                  <option value="1">Toyota</option>
                  <option value="2">Audi</option>
                  <option value="3">BMW</option>
                  <option value="4">Ford</option>
                  <option value="5">HyunDai</option>
                </select>
              </div>
              <div className="col">
                <select className="custom-select" id="inputGroupSelect01">
                  <option selected>Please Select XXX</option>
                  <option value="1">XXX</option>
                  <option value="2">XXX</option>
                  <option value="3">XXX</option>
                </select>
              </div>
            </div>
            <div className="row mb-5 offer">
              <button className="btn btn-primary">Get Instant Offer</button>
            </div>
          </div>
        </div>
        <Carousel />
      </section>
    )
  }
}

export default GetQuotePage;


