import React from 'react';
import './footer.css';

class Footer extends React.Component{
	render(){
		return (
			<div className="footer">
				<div className="row footer-content">
					<div className="col">
						<div className="link">Phone Number: <a className="link" href="">1234567890</a></div>
					</div>
					<div className="col">
						Copyright&nbsp;&copy;&nbsp;Digital&nbsp;Automotive&nbsp;Sales
					</div>
					<div className="col">
						<a className="link" href="/contact-us">Contact Us</a>
					</div>
				</div>
			</div>
		);
	}
}

export default Footer;


