import React from 'react';
import { connect } from 'react-redux';
import { withRouter, Route } from 'react-router-dom';
import Header from '../components/header';
import Footer from '../components/footer';
import HomePage from '../pages/home-page';
import GetQuotePage from '../pages/get-quote-page';
import CarsPage from '../pages/cars-page';
import ServicesPage from '../pages/services-page';
import TermsOfServices from '../pages/terms-of-services-page';
import ContactUsPage from '../pages/contact-us';

class App extends React.Component {
  render() {
    return (
      <div className="app">
        <Header />
        <main>
          <Route exact path="/" component={HomePage} />
          <Route exact path="/get-a-quote" component={GetQuotePage} />
          <Route exact path="/cars" component={CarsPage} />
          <Route exact path="/services" component={ServicesPage} />
          <Route exact path="/terms-of-services" component={TermsOfServices} />
          <Route exact path="/contact-us" component={ContactUsPage} />
        </main>
        <Footer />
      </div>
    )
  }
}

export default withRouter(
  connect(
    null,
    null
  )(App)
);


