import React from 'react';
import VideoPlayer from '../../components/video-player';
import GoogleMap from '../../components/google-map';

import './home-page.css';

class HomePage extends React.Component {
  render() {
    return (
      <section className="home-section">
        <div className="video-container">
          <VideoPlayer />
        </div>
        <div className="button-menu-container">
        <div className="content-container">
          <div className="row">
            <div className="col text-center">
              <a className="btn btn-primary" href="/get-a-quote">GET A QUOTE</a>
            </div>
            <div className="col text-center">
              <a className="btn btn-primary" href="/cars">CARS</a>
            </div>
            <div className="col text-center">
              <a className="btn btn-primary" href="/services">SERVICES</a>
            </div>
            <div className="col text-center">
              <a className="btn btn-primary" href="/terms-of-services">TERMS OF SERVICES</a>
            </div>
          </div>
        </div>
        </div>
        <div className="location-container">
          <div className="content-container">
            <div className="map">
              <GoogleMap lon={-84.386330} lat={33.753746} />
            </div>
          </div>
        </div>
      </section>
    )
  }
}

export default HomePage;


