import React from 'react';
import './contact-us.css';

class ContactUsPage extends React.Component {
  render() {
    return(
      <div className="contact-us">
        <div className="content-container">
          <h1 className="text-center mt-3 mb-3">Contact Us</h1>
          <form className="contact-form">
            {/* First Name */}
            <div className="form-group row">
              <label htmlFor="inputFirstName" className="col-sm-2 col-form-label">First Name:</label>
              <div className="col-sm-10">
                <input
                  type="text"
                  className="form-control"
                  id="inputFirstName"
                  placeholder="First Name"
                />
              </div>
            </div>
            {/* Last Name */}
            <div className="form-group row">
              <label htmlFor="inputLastName" className="col-sm-2 col-form-label">Last Name:</label>
              <div className="col-sm-10">
                <input
                  type="text"
                  className="form-control"
                  id="inputLastName"
                  placeholder="Last Name"
                />
              </div>
            </div>
            {/* Phone Number */}
            <div className="form-group row">
              <label htmlFor="inputPhoneNumber" className="col-sm-2 col-form-label">Phone Number:</label>
              <div className="col-sm-10">
                <input
                  type="text"
                  className="form-control"
                  id="inputPhoneNumber"
                  placeholder="Phone Number"
                />
              </div>
            </div>
            {/* Email Address */}
            <div className="form-group row">
              <label htmlFor="inputEmailAddress" className="col-sm-2 col-form-label">Email Address:</label>
              <div className="col-sm-10">
                <input
                  type="text"
                  className="form-control"
                  id="inputEmailAddress"
                  placeholder="Email Address"
                />
              </div>
            </div>
            {/* Message */}
            <div className="form-group row">
              <label htmlFor="textMessage" className="col-sm-2 col-form-label">Message:</label>
              <div className="col-sm-10">
                <textarea
                  className="message"
                  id="textMessage"
                  placeholder="Message"
                />
              </div>
            </div>
            {/* options */}
            <div className="form-group row">
              <label className="col-sm-4 col-form-label">
                How may we contact you:
              </label>
              <div className="col-sm-4">
                <label>
                  <input
                    type="radio"
                    name="optcontact"
                    value="phone"
                  />
                  &nbsp;By Phone
                </label>
              </div>
              <div className="col-sm-4">
                <label>
                  <input
                    type="radio"
                    name="optcontact"
                    value="email"
                  />
                  &nbsp;By Email
                </label>
              </div>
            </div>
            {/* checkboxes */}
            <div className="form-group row">
              <label className="col-sm-4 col-form-label">
                What time should we contact you:
              </label>
              <div className="col-sm-4">
                <label>
                  <input
                    type="checkbox"
                    name="mon"
                    value="phone"
                  />
                  &nbsp;Mon
                </label>
              </div>
              <div className="col-sm-4">
                <label>
                  <input
                    type="checkbox"
                    name="day"
                    value="email"
                  />
                  &nbsp;Day
                </label>
              </div>
            </div>
            <div className="form-group row submit-row">
              <button type="submit" className="btn btn-primary">
                Submit
              </button>
            </div>
          </form>
          <div className="row">
            <div className="offset-sm-4 col-sm-4">
              <div className="row">
                <div className="col">
                  <h1><i class="fab fa-linkedin"></i></h1>
                </div>
                <div className="col">
                  <h1><i class="fab fa-facebook"></i></h1>
                </div>
                <div className="col">
                  <h1><i class="fab fa-twitter-square"></i></h1>
                </div>
                <div className="col">
                  <h1><i class="fab fa-instagram"></i></h1>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ContactUsPage;


